import { Component, OnInit } from '@angular/core';
// import { DataService } from '../data.service';

import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import {
  Draw,
  User,
  ChatContainer,
  Chat,
  JoinData,
  CanvasController,
  game_cmd_Handler,
  UserContainer,
} from '../draw';
import {PhaseType} from "../message"
import { io } from 'socket.io-client';
import { NgControlStatusGroup } from '@angular/forms';

@Component({
  selector: '',
  templateUrl: './room.component.html',
  styleUrls: ['./room.component.css'],
})
export class RoomComponent implements OnInit {
  private mousedown: boolean = false;
  myName: string;

  hostUser: string;
  isHost: boolean = false;

  users: UserContainer;
  chatList: ChatContainer;
  gameHandler: game_cmd_Handler;

  private socket: any;
  private mySelf: User;
  ROOM_ID: string = "7777";

  public guess: String; // Î≥¥ÎÇº Ï†ïÎãµ
  public MsgToSend: string; // Î≥¥ÎÇº Ï±ÑÌåÖ Î©îÏãúÏßÄ

  constructor(private route: ActivatedRoute) {
    this.ROOM_ID = route.snapshot.params['roomID'];
    this.mySelf = new User('user' + Math.floor(Math.random() * 1000));
    this.myName = this.mySelf.getName();
  }
  sendChat(): void {
    this.socket.emit('chat-msg', this.MsgToSend);
  }

  checkGuess(): void {
    console.log(this.guess + ' guess Ï†ÑÏÜ°');
    this.socket.emit('game-msg', this.guess);
  }
  selectWord0(): void {
    if (this.gameHandler.myTurn) {
      this.socket.emit('game-msg', 0); //0, 1, 2 ÏÇ¨Ïù¥ Ïù∏Îç±Ïä§
    }
  }
  selectWord1(): void {
    if (this.gameHandler.myTurn) {
      this.socket.emit('game-msg', 1); //0, 1, 2 ÏÇ¨Ïù¥ Ïù∏Îç±Ïä§
    }
  }
  selectWord2(): void {
    if (this.gameHandler.myTurn) {
      this.socket.emit('game-msg', 2); //0, 1, 2 ÏÇ¨Ïù¥ Ïù∏Îç±Ïä§
    }
  }
  gameStart(): void {
    let gameSetting = {
      type: 'start',
      data: {
        round: 2,
        timeout: 10,
      },
    };
    this.socket.emit('game-cmd', gameSetting);
  }
  toggleMouseDown(b: boolean): void {
    this.mousedown = b;
  }

  handleMouseEnter(e: any): void {
    if (
      this.mousedown &&
      this.gameHandler.myTurn &&
      this.gameHandler.phase == PhaseType.guess
    ) {
      //e : TouchEvent
      //e.touches : TouchList
      //e.touches[0] : Touch
      //e.touches[0].pageX 
      var drawData: Draw = {X:0, Y:0};
      if( e instanceof MouseEvent){
        drawData = {X: e.offsetX, Y: e.offsetY}
      }else if(e instanceof TouchEvent){
        console.log(e)
        drawData = { X: e.touches[0].pageX, Y: e.touches[0].pageY }
      }
      // console.log(e)
      // console.log('X: ' + e.pageX + ', Y: ' + e.pageY); //-> "mouseenter"
      CanvasController.getInstance().draw(drawData.X, drawData.Y);


      this.socket.emit('draw cmd', drawData);
      // this.dataService.sendMessage(data);
    }
  }
  initInstances(reason){
    if(reason){
      console.log("disconnected")
      console.log(reason)
    }
    var canvas: any = document.getElementById('canvas');

    CanvasController.createInstance(canvas);
        game_cmd_Handler.createInstance(this.mySelf);
        this.chatList = ChatContainer.getInstance();
        this.gameHandler = game_cmd_Handler.getInstance();
        this.users = UserContainer.getInstance();
  }
  ngOnInit(): void {
    // this.socket = io('ws://localhost:9999');
    this.socket = io('ws://catchm1nd.herokuapp.com/');

    console.log(this.socket);
    this.initInstances(null)

    this.socket.on('connect', () => {

      let joinData: JoinData = {
        roomID: this.ROOM_ID,
        user: this.mySelf,
      };
      console.log(joinData);
      this.socket.emit('join', joinData);

      this.socket.on('draw cmd', (data) => {
        console.log(data);
        CanvasController.getInstance().draw(data.X, data.Y);
      });

      this.socket.on('disconnect', this.initInstances);

      this.socket.on('sys-msg', (msg) => {
        // 1. Ìò∏Ïä§Ìä∏ Î≥ÄÍ≤Ω
        if (msg.type == 'host-changed') {
          this.hostUser = msg.data;
          this.isHost = this.hostUser == this.myName;
          this.chatList.push(
            Chat.SysMsg('Î∞©Ïùò Ìò∏Ïä§Ìä∏Í∞Ä ' + msg.data + 'Î°ú Î≥ÄÍ≤ΩÎêòÏóàÏäµÎãàÎã§.')
          );
        } else if (msg.type == 'user-welcome') {
          // ÏÉàÎ°úÏò® Ïú†Ï†ÄÏóêÍ≤å Í∏∞Ï°¥ Î¶¨Ïä§Ìä∏ Ï†ÑÎã¨
          // msg.data {
          //   host: string,
          //   users: user[],
          //   participants: user[]
          // }
          console.log('sys-msg : user-welcome received!!');
          console.log(msg.data);
          this.hostUser = msg.data.host;
          this.isHost = this.hostUser == this.myName;
          this.users.setUsers(msg.data.users as User[]);
          this.users.setParticipants(msg.data.participants);
        } else if (msg.type == 'user-join') {
          // Ïú†Ï†Ä Ï†ëÏÜç
          console.log('userjoin' + msg.data);
          if (msg.data != this.mySelf.getName()) {
            this.users.add(new User(msg.data));
          }
          this.chatList.push(Chat.SysMsg(msg.data + 'Í∞Ä ÏûÖÏû•ÌñàÏäµÎãàÎã§.'));
        } else if (msg.type == 'user-leave') {
          // Ïú†Ï†Ä msg.dataÍ∞Ä ÎÇòÍ∞ê
          // ÏÇ≠Ï†ú
          console.log('userleave ' + msg.data);
          this.users.leaveUser(msg.data);
          this.chatList.push(Chat.SysMsg(msg.data + 'Í∞Ä Í≤åÏûÑÏùÑ Îñ†ÎÇ¨ÏäµÎãàÎã§.'));
        }
      });

      this.socket.on('chat-msg', (msg) => {
        this.chatList.push(Chat.UserMsg(msg.from, msg.data));
        //Ïä§ÌÅ¨Î°§ ÏµúÌïòÎã® Í≥†Ï†ï
        let ele = document.querySelector('.chattingView');
        ele.scrollTop = ele.scrollHeight;
      });

      this.socket.on(
        'game-msg',
        function (msg) {
          game_cmd_Handler.getInstance().msg_Handler(msg);
        }.bind(this)
      );

      this.socket.on(
        'game-cmd',
        function (cmd) {
          game_cmd_Handler.getInstance().cmd_Handler(cmd);
        }.bind(this)
      );
    });

    var canvas: any = document.getElementById('canvas');

    console.log(canvas);

    canvas.addEventListener(
      'mousemove',
      function (e) {
        this.handleMouseEnter(e);
      }.bind(this),
      false
    );
    canvas.addEventListener(
      'mousedown',
      function (e) {
        this.mousedown = true;
      }.bind(this),
      false
    );
    canvas.addEventListener(
      'mouseup',
      function (e) {
        this.mousedown = false;
      }.bind(this),
      false
    );
    canvas.addEventListener(
      'touchstart',
      function (e) {
        e.preventDefault();
        console.log("touchstart")
        console.log(e)
        this.mousedown = true;
      }.bind(this),
      false
    );
    canvas.addEventListener(
      'touchmove',
      function (e) {
        e.preventDefault();
        this.handleMouseEnter(e);
      }.bind(this),
      false
    );
    canvas.addEventListener(
      'touchend',
      function (e) {
        e.preventDefault();
        this.mousedown = false;
      }.bind(this),
      false
    );
  }
}
