export class Chat {
  private constructor(isSys: boolean, from: User, data: string) {
    this.isSystem = isSys;
    this.from = from;
    this.data = data;
  }
  static SysMsg(msg: string): Chat {
    return new Chat(true, null, msg);
  }
  static UserMsg(user: User, msg: string): Chat {
    return new Chat(false, user, msg);
  }

  isSystem: boolean;
  from: User;
  data: string;
}

